import React from 'react';
import { useDispatch } from 'react-redux';

import { setSort } from '../../redux/Filter/slice';
import { SortType } from '../../redux/Filter/types';

import styles from './Sort.module.scss';

type PropsOrderType = {
  value: SortType,
  orderType: string,
  setOrderType: (orderType: string) => void,
};

export let list: SortType[] = [
  { name: '–ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏', sortProperty: 'rating' },
  { name: '—Ü–µ–Ω–µ', sortProperty: 'price' },
  { name: '–∞–ª—Ñ–∞–≤–∏—Ç—É', sortProperty: 'title' },
];

const Sort: React.FC<PropsOrderType> = React.memo(({ value, orderType, setOrderType }) => {
  let dispatch = useDispatch();

  let sortRef = React.useRef<HTMLDivElement>(null);
  //–ü–æ–ø–∞–ø —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∏(–æ—Ç–∫—Ä—ã—Ç—å - –∑–∞–∫—Ä—ã—Ç—å)
  let [open, setOpen] = React.useState<boolean>(false);

  let onClickListItem = (obj: SortType) => {
    dispatch(setSort(obj));
    setOpen(false);
  };

  React.useEffect(() => {
    let handleClickOutside = (event: MouseEvent) => {
      if (sortRef.current && !event.composedPath().includes(sortRef.current)) {
        setOpen(false);
      }
    };

    document.body.addEventListener('click', handleClickOutside);

    return () => {
      document.body.removeEventListener('click', handleClickOutside);
    };
  }, []);

  return (
    <div ref={sortRef} className={styles.sort}>
      <div className={styles.sortLabel}>
        <svg
          className={open ? `${styles.open}` : ''}
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg">
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b>–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ –ø–æ:</b>
        <span onClick={() => setOpen(!open)}>{value.name}</span>
        <div className={styles.sortLabelRanking}>
          <button
            className={orderType === 'asc' ? styles.activeButton : ''}
            onClick={() => setOrderType('asc')}>
            ‚Üë
          </button>
          <button
            className={orderType === 'desc' ? styles.activeButton : ''}
            onClick={() => setOrderType('desc')}>
            ‚Üì
          </button>
        </div>
      </div>
      {open && (
        <div className={styles.sortPopup}>
          <ul>
            {list.map((obj, index) => (
              <li
                key={index}
                onClick={() => onClickListItem(obj)}
                className={value.sortProperty === obj.sortProperty ? 'active' : ''}>
                {obj.name}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
})

export default Sort;
